import * as React from 'react';

import './Root.scss';
import { Header } from '../../components/Header/Header';
import { Menu } from '../../components/Menu/Menu';
import { Whois } from '../../components/Whois/Whois';
import { Form } from '../../components/Form/Form';
import { Grid } from '../../components/Grid/Grid';
import {Project} from '../../components/Project/Project';

interface Props{}
interface State {
    page: number
    project: number
}


export class Root extends React.Component<Props,State> {

    constructor(props: Props) {
        super(props);
        this.state = {
            page :  1,
            project: 2
        }
    }

    handlePage = (state: number) => {
        //this.setState({ page: state });
        console.log(state);
        this.setState({page: state})
    }

    handleProjectSelection = (state:number)=>{
        console.log(state);
        this.setState({project:state,page:3});
    }

    pageChange(n: number) {
        switch (n) {
            case 0:
                return <Grid changePage={this.handleProjectSelection}/>
            case 1:
                return <Whois/>
            case 2:
                return <Form/>
            case 3:
                return <Project num={this.state.project}/>
        }
    }

    render() {
        return <div>
            <Menu pageChanger={this.handlePage} />
            <div id="content">
                {this.pageChange(this.state.page)}
            </div>
        </div>
    }
}























