import * as React from 'react';

import './Menu.scss';

interface Props{
    pageChanger:any
}

export const Menu = (props:Props) => {
    return <div className="Menu">
        <div className="img">
            <img src="./assets/img/LogoPersonal.png" alt="" />
        </div>
        <div className="menu">
            <a href="" onClick={(e)=>{e.preventDefault(); props.pageChanger(0)}}>
                <span>Portfolio </span>
            </a>
            <a href="" onClick={(e)=>{e.preventDefault();props.pageChanger(1)}}>
                <span>Who's Pablo?</span>
            </a>
            <a href="" onClick={(e)=>{e.preventDefault();props.pageChanger(2)}}>
                <span>Contact</span>
            </a></div>
    </div>;
}